.main-catalog__products-wrap {
  position: relative;
  min-height: 20rem;
}
.main-catalog__products-wrap.is-loading .catalog-products-content {
  -webkit-filter: blur(4px);
  filter: blur(4px);
  pointer-events: none;
}
.main-catalog__products-wrap.is-loading::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 4rem;
  height: 4rem;
  margin: -2rem 0 0 -2rem;
  border: 3px solid rgba(0, 0, 0, 0.1);
  border-top-color: currentColor;
  border-radius: 50%;
  -webkit-animation: catalog-loader-spin 0.7s linear infinite;
  animation: catalog-loader-spin 0.7s linear infinite;
  z-index: 1;
}
@-webkit-keyframes catalog-loader-spin {
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes catalog-loader-spin {
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.peremena {
  &__title {
    font-size: 4rem;
    font-weight: 300;
    color: #4a9b8e;
    margin-bottom: 0;
    letter-spacing: 2px;
    line-height: 1;
  }

  &__title span {
    display: inline-block;
    transition: font-weight 0.2s ease;
    font-weight: 300;
    font-feature-settings: "tnum";
    font-variant-numeric: tabular-nums;
    width: 0.6em;
    text-align: right;

    &.m {
      width: 0.8em;
    }
  }

  &__subtitle {
    font-size: 4rem;
    font-weight: 500;
    color: #000;
    margin-bottom: 0;
    line-height: 1.1;
  }

  &__description {
    font-size: 4rem;
    font-weight: 300;
    color: #000;
    line-height: 1.1;
  }
}
